.modal {
    font-size: 12px;
  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
    padding: 10px 5px;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }

/* Popup Instructions */

.popup-content {
    margin:auto;
    background: black;
    width:50%;
    height: 50%;
    padding:5px;
    border:1px solid #d7d7d7};

    
[role=tooltip].popup-content {
    width:200px;
    box-shadow:0 0 3px rgba(0,0,0,.16);
    border-radius:5px;
    color: black;

}

.popup-overlay {
    background:rgba(0,0,0,.5);
}

[data-popup=tooltip].popup-overlay {
    background:transparent;
}

.popup-arrow {
    -webkit-filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
    filter:drop-shadow(0 -3px 3px rgba(0,0,0,.16));
    color:rgb(173, 15, 15);
    stroke-width:2px;stroke:#d7d7d7;
    stroke-dasharray:30px;
    stroke-dashoffset:-54px;
} 
